import React, { useState, useEffect } from 'react'
import { Breadcrumb } from 'react-bootstrap'
import { Link } from "react-router-dom"
import Notification from '../Notification'
import { LinkContainer } from 'react-router-bootstrap'
import desimaalijarjestelma from '../../desimaali.png'
import binaarijarjestelma from '../../binaarijarjestelma.png'
import binaarilukutaulukko from '../../binaarilukutaulukko.png'

const BinToDecCon = () => {
    const [binary, setBinary] = useState('1010')
    const [decimal, setDecimal] = useState('')
    const [message, setMessage] = useState('')
    const [errorMessage, setErrorMessage] = useState('')

    useEffect(() => {
        handleChange(binary)
        document.title = 'Binri-desimaalilukumuunnin - Laske & Muunna'
    })

    const handleChange = (value) => {
        setBinary(value)
        if (validateBinary(value)) {
            setDecimal(convertBinToDec(value))
            if (value.length > 0) {
                setMessage(`${binary} on desimaalilukuna ${decimal}`)
                setErrorMessage('')
            } else {
                setMessage('')
                setErrorMessage('')
            }
        } else {
            setErrorMessage('Virheellinen binriluku. Syt ainoastaan numeroita 0 tai 1.')
            setMessage('')
        }
    }

    const validateBinary = (bin) => {
        for (let i = 0; i < bin.length; i++) {
            if (bin.charAt(i) !== '0' && bin.charAt(i) !== '1' && bin.charAt(i) !== ' ') {
                return false
            }
        }
        return true
    }

    const convertBinToDec = (bin) => {
        const convertedDecimal = parseInt((bin + '').replace(/[^01]/gi, ''), 2)
        return convertedDecimal
    }

    return (
        <div>
            <Breadcrumb>
                <LinkContainer to="../">
                    <Breadcrumb.Item>
                        Alkuun
                </Breadcrumb.Item>
                </LinkContainer>
                <LinkContainer to="./">
                    <Breadcrumb.Item>
                        Yksikkmuuntimet
                </Breadcrumb.Item>
                </LinkContainer>
                <Breadcrumb.Item active>
                    Binri-desimaalilukumuunnin
                </Breadcrumb.Item>
            </Breadcrumb>
            <div>
                <div style={{ maxWidth: '90%' }}>
                    <h2>
                        Binri-desimaalilukumuunnin
            </h2>
                </div>
                <div className='calculator'>
                    <p>Tll muuntimella voit muuntaa binriluvun niin sanotuksi normaaliksi luvuksi eli desimaaliluvuksi.</p>
                    <div>
                        <form>
                            <p>
                                <b>Syt binriluku:</b> <input value={binary} onChange={({ target }) => handleChange(target.value)}>
                                </input>
                            </p>
                        </form>
                        <Notification message={message} result={decimal} errorMessage={errorMessage} />
                        <p>Katso mys <Link to="./desimaali-binaari-muunnin">desimaali-binrilukumuunnin</Link>.</p>
                    </div>
                </div>
            </div>
            <hr />
            <h4>Desimaalijrjestelm</h4>
            <p>Normaalisti kytmme arkielmss normaalia 10-numeroista lukujrjestelm (luvut 0-9), eli desimaalijrjestelm.<br /> Numeron paikka luvussa on merkitsev ja kullakin numerolla on erilainen painoarvo sen mukaan, mik on numeron sijainti
            luvussa. Numeroiden painoarvot kasvavat oikealta vasemmalle: 1, 10, 100, 1000, 10000... <br />10 numerojrjestelmss kantaluku on 10 ja numeroiden painoarvot saadaan kantaluvun potensseista: <var>1=10<sup>0</sup>, 10=10<sup>1</sup>, 100=10<sup>2</sup>, 1 000=10<sup>3</sup>, 10 000=10<sup>4</sup></var></p>
            <img src={desimaalijarjestelma} alt='Desimaaliluku' style={{ maxWidth: '80%' }} />
            <h4>Binrijrjestelm</h4>
            <p>
                Binrijrjestelm on teknisesti yksinkertaisin lukujrjestelm. Binrijrjestelm on kaksijrjestelm, jossa tarvitaan vain kahta numeroa: 0 ja 1.
            <img src={binaarilukutaulukko} alt='Binrilukutaulukko' style={{ maxWidth: '100%', float: 'right', margin: '20px' }} />
                <br />
            Tietokoneiden on vaikeaa kytt 10-numerojrjestelm merkkimrn vuoksi, joten tietokoneiden toiminta perustuukin yksinkertaisempaan binrijrjestelmn. Binriluvuilla on helppo esitt erilaisia ilmiit, kuten: shk kulkee / ei kulje, ovi on auki / kiinni, toimii / ei toimi ja niin edelleen.
            <br />
            Binrilukujrjestelmss lukujen painoarvot saadaan kantaluvun (2) potensseina, samalla tavalla kuin 10-jrjestelmss.<br />
                <var>1=2<sup>0</sup>, 2=2<sup>1</sup>, 4=2<sup>2</sup>, 8=2<sup>3</sup>, 16=2<sup>4</sup>, 32=2<sup>5</sup>, 64=2<sup>6</sup></var><br />
            Lukujen painoarvot kasvaa oikealta vasemmalle: 1, 2, 4, 8, 16, 32, 64, 128, 256, 512 jne.<br />
            Yll luetellut luvut saattaa nytt hyvin tutuilta, koska ne tulevat jatkuvasti vastaan tietotekniikassa.
            <br />
            Binriluku saadaan kerrottua helposti kahdella lismll 0 luvun pern, eli esimerkiksi 1010B on desimaalilukuna 10 ja 10100B on desimaalilukuna 20.
            <img src={binaarijarjestelma} alt='Binrilukujrjestelm' style={{ maxWidth: '100%' }} />
                <br />
            </p>
        </div>
    )
}

export default BinToDecCon