import React, { useState, useEffect } from 'react'
import Notification from '../Notification'
import { Breadcrumb } from 'react-bootstrap'
import { LinkContainer } from "react-router-bootstrap"
import { Link } from "react-router-dom"
import desimaalijarjestelma from '../../desimaali.png'
import binaarijarjestelma from '../../binaarijarjestelma.png'
import binaarilukutaulukko from '../../binaarilukutaulukko.png'

const DecToBinCon = () => {
    const [decimal, setDecimal] = useState('10')
    const [binary, setBinary] = useState('')
    const [message, setMessage] = useState('')
    const [errorMessage, setErrorMessage] = useState('')

    useEffect(() => {
        handleChange(decimal)
        document.title = 'Desimaali-binrilukumuunnin - Laske & Muunna'
    })

    const handleChange = (value) => {
        setDecimal(value)
        setBinary(convertDectoBin(decimal))
        if (isNaN(value)) {
            setErrorMessage('Sytetty luku ei ole numero')
        } else if (value.length !== 0) {
            setMessage(`${decimal} on binrilukuna ${binary}`)
        } else {
            setMessage('')
            setErrorMessage('')
        }
    }

    const convertDectoBin = (dec) => {
        if (dec < 0) {
            dec = 0xFFFFFFFF + dec + 1
        }
        return parseInt(dec, 10).toString(2);
    }

    return (
        <div>
            <Breadcrumb>
                <LinkContainer to="../">
                    <Breadcrumb.Item>
                        Alkuun
                </Breadcrumb.Item>
                </LinkContainer>
                <LinkContainer to="./">
                    <Breadcrumb.Item>
                        Yksikkmuuntimet
                </Breadcrumb.Item>
                </LinkContainer>
                <Breadcrumb.Item active>
                    Desimaaliluku-binrilukumuunnin
                </Breadcrumb.Item>
            </Breadcrumb>
            <div>
                <h2>
                    Desimaali-binrilukumuunnin
            </h2>
                <div className="calculator">
                    <p>Tll muuntimella voit muuttaa niin sanotun normaalin luvun, eli desimaaliluvun binriluvuksi. Syt desimaaliluku muuntimeen, niin muunnin nytt luvun binrilukuna.</p>
                    <form>
                        <p>
                            Syt luku: <input value={decimal} onChange={({ target }) => handleChange(target.value)}>
                            </input>
                        </p>
                    </form>
                    <Notification message={message} result={binary} errorMessage={errorMessage} />
                    <p>Katso mys <Link to="./binaari-desimaali-muunnin">binri-desimaalilukumuunnin</Link>.</p>
                </div>
            </div>
            <hr />
            <h4>Desimaalijrjestelm</h4>
            <p>Normaalisti kytmme arkielmss normaalia 10-numeroista lukujrjestelm (luvut 0-9), eli desimaalijrjestelm.<br /> Numeron paikka luvussa on merkitsev ja kullakin numerolla on erilainen painoarvo sen mukaan, mik on numeron sijainti
            luvussa. Numeroiden painoarvot kasvavat oikealta vasemmalle: 1, 10, 100, 1000, 10000... <br />10 numerojrjestelmss kantaluku on 10 ja numeroiden painoarvot saadaan kantaluvun potensseista: <var>1=10<sup>0</sup>, 10=10<sup>1</sup>, 100=10<sup>2</sup>, 1 000=10<sup>3</sup>, 10 000=10<sup>4</sup></var>
                <img src={desimaalijarjestelma} alt='Desimaaliluku' style={{ maxWidth: '100%' }} />
            </p>
            <h4>Binrijrjestelm</h4>
            Binrijrjestelm on teknisesti yksinkertaisin lukujrjestelm. Binrijrjestelm on kaksijrjestelm, jossa tarvitaan vain kahta numeroa: 0 ja 1.
            <p>
                <img src={binaarilukutaulukko} alt='Binrilukutaulukko' style={{ maxWidth: '100%', float: 'right', margin: '20px' }} />
                <br />
            Tietokoneiden on vaikeaa kytt 10-numerojrjestelm merkkimrn vuoksi, joten tietokoneiden toiminta perustuukin yksinkertaisempaan binrijrjestelmn. Binriluvuilla on helppo esitt erilaisia ilmiit, kuten: shk kulkee / ei kulje, ovi on auki / kiinni, toimii / ei toimi ja niin edelleen.
            <br />
            Binrilukujrjestelmss lukujen painoarvot saadaan kantaluvun (2) potensseina, samalla tavalla kuin 10-jrjestelmss.<br />
                <var>1=2<sup>0</sup>, 2=2<sup>1</sup>, 4=2<sup>2</sup>, 8=2<sup>3</sup>, 16=2<sup>4</sup>, 32=2<sup>5</sup>, 64=2<sup>6</sup></var><br />
            Lukujen painoarvot kasvaa oikealta vasemmalle: 1, 2, 4, 8, 16, 32, 64, 128, 256, 512 jne.<br />
            Yll luetellut luvut saattaa nytt hyvin tutuilta, koska ne tulevat jatkuvasti vastaan tietotekniikassa.
            <br />
            Binriluku saadaan kerrottua helposti kahdella lismll 0 luvun pern, eli esimerkiksi 1010B on desimaalilukuna 10 ja 10100B on desimaalilukuna 20.
            <img src={binaarijarjestelma} alt='Binrilukujrjestelm' style={{ maxWidth: '100%' }} />
                <br />

            </p>
        </div>
    )
}
export default DecToBinCon